@font-face {
  font-family: "BloggerSans-Bold";
  font-style: normal;
  font-weight: bold;
  font-display: swap;
  src: local("Blogger Sans Bold"),
    local("BloggerSans-Bold"),
    url("../fonts/bloggersans-bold.woff2") format("woff2"),
    url("../fonts/bloggersans-bold.woff") format("woff");
}

